@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body{min-height:100vh;background-color:#f2f2f2}
.BottomNavbar{display:flex;position:fixed;bottom:0;left:0;width:100%;height:56px;border-radius:8px;background-color:#fff}.BottomNavbar .pages{flex:1 1;display:flex;justify-content:space-around;align-items:center;margin:0 18px}.BottomNavbar .pages .page{position:relative;display:flex;flex-direction:column;align-items:center;padding-bottom:10px;width:48px;color:#bdbdbd}.BottomNavbar .pages .page .text{position:absolute;bottom:0;font-size:10px;line-height:10px}.BottomNavbar .pages .page svg{fill:#bdbdbd}.BottomNavbar .pages .page.active{color:#fd0054}.BottomNavbar .pages .page.active svg{fill:#fd0054}
.Header{padding:30px 25px;height:180px;box-sizing:border-box;margin-bottom:-56px}.Header .app-title,.Header .app-subtitle{color:#000}.Header.withBackground{background:linear-gradient(244.36deg, #A80038 0%, #FD0054 100%),#fff}.Header.withBackground .app-title,.Header.withBackground .app-subtitle{color:#fff}
.HomePage{position:relative;height:calc(100vh - 124px - 56px)}.HomePage>.content{margin:0 12px}
.ProductCard{display:flex;justify-content:flex-start;box-sizing:border-box}.ProductCard .product-image{width:54px;height:72px}.ProductCard .content{display:flex;flex:1 1;flex-direction:column;margin-left:12px}.ProductCard .content .expiry{font-weight:bold;font-size:10px;line-height:10px}.ProductCard .content .name{font-weight:bold;font-size:16px}.ProductCard .content .footer{display:flex;justify-content:flex-start;margin-top:auto}.ProductCard .content .footer .nutriscore,.ProductCard .content .footer .novagroup{height:32px}.ProductCard .content .footer .novagroup{margin-left:10px}.ProductCard .content .footer .qta{font-weight:bold;font-size:16px;margin-left:auto;margin-top:auto}
.FloatingActionButton{height:56px;width:56px;border-radius:50%;background-color:#fd0054;box-shadow:0 1px 1px rgba(0,0,0,.25);display:flex;align-items:center;justify-content:center;position:absolute;bottom:20px;right:20px;color:#fff}.FloatingActionButton svg{fill:#fff}
.ProductScanModal{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:#fff;z-index:2;transform:translateY(100%) translateZ(0);will-change:transform;transition:transform 300ms}.ProductScanModal .header{position:relative;display:flex;justify-content:center;align-items:center;height:72px}.ProductScanModal .header .app-title{font-size:18px;line-height:18px}.ProductScanModal .header .close-btn{position:absolute;left:24px;height:24px}.ProductScanModal .header .close-btn svg{transform:rotate(45deg);fill:#bdbdbd}.ProductScanModal.open{transform:none}.ProductScanModal .reader{width:100%;height:calc(100vh - 72px - 38px);background-color:#f2f2f2}.ProductScanModal .reader.searching{overflow:auto}.ProductScanModal .reader .SearchContainer{margin-top:10px;margin-bottom:20px;min-height:100%}.ProductScanModal .search-container{height:32px;margin-bottom:6px}.ProductScanModal .search-container form{height:100%}.ProductScanModal .search-container form input{height:100%;width:100%;border:none;margin:0;padding:0 10px;font-size:18px}
.SearchContainer{padding:0 18px;display:flex;flex-direction:column;align-items:center}.SearchContainer .loading,.SearchContainer .waiting{margin-top:auto;margin-bottom:auto}.SearchContainer .SearchResult{width:100%;margin:5px 0}
.SearchResult{display:flex;justify-content:flex-start;align-items:center;box-sizing:border-box}.SearchResult .product-image{width:54px;height:72px}.SearchResult .name{font-weight:bold;font-size:16px;margin-left:10px}.SearchResult:active{background-color:#f2f2f2}
.ProductInsertModal{position:fixed;top:0;left:0;width:100vw;height:100vh;background-color:#fff;z-index:2;transform:translateY(100%) translateZ(0);will-change:transform;transition:transform 300ms}.ProductInsertModal .header{position:relative;display:flex;justify-content:center;align-items:center;height:72px}.ProductInsertModal .header .app-title{font-size:18px;line-height:18px}.ProductInsertModal .header .close-btn{position:absolute;left:24px;height:24px}.ProductInsertModal .header .close-btn svg{transform:rotate(45deg);fill:#bdbdbd}.ProductInsertModal.open{transform:none}.ProductInsertModal .header .close-btn svg{transform:none}.ProductInsertModal .content{background-color:#f2f2f2;height:calc(100vh - 72px);padding:20px 16px;display:flex;flex-direction:column}.ProductInsertModal .content .product-head{display:flex}.ProductInsertModal .content .product-head .product-image{min-width:88px;max-width:88px;height:110px;margin-right:10px}.ProductInsertModal .content .product-head .product-info{display:flex;flex-direction:column;margin-left:5px}.ProductInsertModal .content .product-head .product-info .product-name{font-weight:bold;font-size:16px}.ProductInsertModal .content .product-head .product-info .description{max-height:100px;overflow-y:auto;line-height:15px;padding-bottom:10px}.ProductInsertModal .content .product-head .product-info .description .title{font-weight:bold;font-size:12px;color:#2b2024}.ProductInsertModal .content .product-head .product-info .description .ingredients{margin-top:10px;font-weight:normal;font-size:10px}.ProductInsertModal .content .nutriscore-container{display:flex;justify-content:center;align-items:center;margin-top:20px}.ProductInsertModal .content .nutriscore-container .nutriscore,.ProductInsertModal .content .nutriscore-container .novagroup{height:49px;margin:0 10px}.ProductInsertModal .content .form{display:flex;flex-direction:column}.ProductInsertModal .content .form .input-group{min-height:68px;margin-top:20px;box-sizing:border-box;display:flex;flex-direction:column}.ProductInsertModal .content .form .input-group .label{font-weight:normal;font-size:12px;line-height:15px}.ProductInsertModal .content .form .input-group input{margin:6px 0 0;border:0;background-color:transparent;font-weight:bold;font-size:18px;line-height:22px}

body{font-family:"Montserrat",sans-serif}.app-title{font-size:21px;line-height:26px}.app-subtitle{font-size:14px;line-height:17px}.card{padding:10px;box-shadow:0 1px 1px rgba(0,0,0,.16);border-radius:8px;background-color:#fff}body{margin:0}
